<template>
  <div class="user-password-wrapper">
    <app-header title="修改密码" />
    <div class="user-password-content">
      <div
        class="password-input van-hairline--bottom"
        v-for="item in inputList"
        :key="item.name"
      >
        <login-input
          max-length="11"
          v-model="item.value"
          :type="item.type"
          :placeholder="item.name"
        >
          <i
            slot="right"
            class="super-app-iconfont pwd-type"
            :class="
              item.type === 'password'
                ? 'super-app-icon-eye-close'
                : 'super-app-icon-eye-open'
            "
            @click="changePasswordType(item)"
          ></i>
        </login-input>
      </div>
      <p class="pwdTip" :class="{ pwdError: pwdError }">
        至少8位，需包含字母、数字、特殊字符中的任意2种
      </p>
      <app-button @click="submit">提交</app-button>
    </div>
  </div>
</template>

<script>
import AppButton from '@components/app-button';
import LoginService from '@services/login-service';
import UserService from '@services/user-service';
import validate from '@src/utils/validate';
import LoginInput from '@components/login-input';

const REG_CHINESE = /[\u4E00-\u9FA5\uF900-\uFA2D]/g;

export default {
  components: {
    AppButton,
    LoginInput,
  },

  data() {
    return {
      pwdError: false,
      inputList: [
        {
          name: '旧密码',
          value: '',
          type: 'password',
        },
        {
          name: '新密码',
          value: '',
          type: 'password',
        },
        {
          name: '确认密码',
          value: '',
          type: 'password',
        },
      ],
    };
  },

  activated() {
    this.pwdError = false;
    this.inputList = [
      {
        name: '旧密码',
        value: '',
        type: 'password',
      },
      {
        name: '新密码',
        value: '',
        type: 'password',
      },
      {
        name: '确认密码',
        value: '',
        type: 'password',
      },
    ];
  },

  methods: {
    changePasswordType(item) {
      item.type = item.type === 'password' ? 'text' : 'password';
    },

    // 修改密码
    submit() {
      for (let item of this.inputList) {
        if (!item.value) {
          return this.$toast(`${item.name}不能为空`);
        }
      }

      const oldPwd = this.inputList[0].value;
      let newPwd = this.inputList[1].value;
      let reNewPwd = this.inputList[2].value;

      if (REG_CHINESE.test(newPwd)) {
        return this.$toast('不能使用中文作为密码');
      }

      if (reNewPwd != newPwd) {
        return this.$toast(`两次输入的密码必须一致`);
      }

      const { level, password } = validate.checkPwd(newPwd);
      newPwd = password;

      if (level < 2) {
        this.pwdError = true;
        return false;
      }

      this.pwdError = false;

      this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
        loadingType: 'spinner',
        message: '正在提交...',
      });

      UserService.updatePwd(oldPwd, newPwd, reNewPwd)
        .then(() => {
          this.$toast.clear();
          this.$toast('修改密码成功,请重新登录');
          setTimeout(() => {
            LoginService.logout();
          }, 500);
        })
        .catch(error => {
          this.$toast.clear();
          this.$toast(error || '修改密码失败，请稍后重试');
        });
    },
  },
};
</script>

