.user-password-wrapper {
  width: 100%;
  height: 100%;
  background: #ffffff;

  .pwd-type {
    margin-left: 25px;
  }

  .user-password-content {
    display: flex;
    flex-direction: column;
    padding: 0 30px;
    margin-top: 40px;

    .password-input {
      height: 60px;
      width: 100%;
      padding: 30px 0 0 0;
      font-size: 14px;
      display: flex;
      align-items: flex-end;

      & > input {
        flex: 1;
        border: none;
        font-size: 14px;
        padding: 5px 0;
      }
    }

    .app-button {
      margin-top: 30px;
    }
  }

  .pwdTip {
    color: #999999;
    margin-top: 10px;
  }

  .pwdError {
    color: #ff3b30;
  }
}
</style>
